import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {UserService} from '../../service/user.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  model: any = {};
  loading = false;
  error = '';

  constructor(
    private router: Router,
    private authenticationService: UserService) { }

  ngOnInit() {
    // reset login status
    this.authenticationService.logout();
  }

  // proceso de login, loading muestra el simbolo de carga encima del boton.
  login() {
    this.loading = true;
    if (this.authenticationService.login(this.model.username, this.model.password)) {
      this.router.navigate(['/app-listado-contratos']);
      } else {
      // login failed
      this.error = 'Usuario o contraseña incorrecto';
      this.loading = false;
    }
  }
  // en caso de no tener cuenta, activamos el registro
  registro() {
    this.router.navigate(['/app-registro']);
  }
}
